import React from 'react';

class Order extends React.Component {

    renderOrder = key => {
        const book = this.props.books[key];
        const count = this.props.order[key];


        return (

            <li className="order-items" key={key}>

                <span> {count} </span>

                {book.name}
                <span className="order-price">{count * book.price} Ft  </span>
                <button className="delete-order" onClick={() => this.props.removeFromOrder(key)}>&times;</button>


            </li>


        );
    };


    render() {

        const orderIds = Object.keys(this.props.order);  // orderIds = fish1, fish2...
        const total = orderIds.reduce((prevTotal, key) => {
            const fish = this.props.books[key];
            const count = this.props.order[key];
            const isAvailable = fish && fish.status === 'available';
            if (isAvailable) {
                return prevTotal + (count * fish.price);
            }
            return prevTotal;
        }, 0);

        return (
            <div className="order-wrap">
                <h2>Order</h2>
                <ul className="order-list">
                    {orderIds.map(this.renderOrder)}
                </ul>



                <div className="total">
                    Total:
                    <strong>{total}</strong>
                    
                </div>
            </div>
        );
    }

}

export default Order;