import React from 'react';
// import {formatePrice} from '../helpers';


class Book extends React.Component {




    handleClick = () => {
        this.props.addToOrder(this.props.index);
    }
    render() {
        // const { image, name, price, desc, status } = this.props.details; <img src={image} alt={name} />
        // const isAvailable = this.props.details.status === "available";
        return (
            <div className="az">
                <li className="menu-book">
                    {/* <img src={require('../images/greek_islands.jpg').default} alt = ""/> */}
                    <img src={this.props.details.image} alt={this.props.details.name} />
                    <h3 className="book-name">{this.props.details.name}
                        {/* <span className="price">{formatPrice(this.props.details.price)}</span> */}
                        <span className="price">{this.props.details.price} Ft</span>
                    </h3>
                    <p>{this.props.details.desc}</p>
                    <button onClick={this.handleClick}>Add To Order</button>
                </li>
            </div>

        );
    }
}
export default Book;