import React from 'react';
import Header from './Header';
import sampleBooks from '../sample-books';
import Book from './Book';
import base from '../base';
import Order from './Order';

class App extends React.Component {

    state = {
        books: {},
        order: {}
    };

    componentDidMount() {
        this.setState({ books: sampleBooks });          // így rögtön betöltődik
        this.ref = base.syncState('order', {
            context: this,
            state: 'order'
        });
    }

    componentDidUpdate() {

    }

    componentWillUnmount() {
        base.removeBinding(this.ref);
    }


    addToOrder = (key) => {
        // 1. Take a copy of state
        const order = { ...this.state.order };
        // 2. Either add to the order or update the number in order
        order[key] = order[key] + 1 || 1;
        // 3. Call setState to update our state object
        this.setState({ order: order });
    }

    // removeFromOrder = key => {
    //     // 1. Take a copy of state
    //     const order = { ...this.state.order };
    //     // 2. Remove that item from order
    //     delete order[key];
    //     // 3. Call setState to update our state object
    //     this.setState({ order });
    // }

    removeFromOrder = key => {
        this.setState({
            order: {[key]: null}
          });
    }

    render() {
        return (
            <div className="App">
                <Header />

                <div className="book-store">
                    <ul className="books">
                        {Object.keys(this.state.books).map(key =>
                            <Book key={key} index={key} details={this.state.books[key]} addToOrder={this.addToOrder} />
                        )}
                    </ul>

                    <Order books={this.state.books} order={this.state.order} removeFromOrder={this.removeFromOrder} />
                </div>

            </div>
        );
    }
}

export default App;